.loader {
  display: block;
  margin: 32px auto;
  border: 5px solid gray;
  border-top: 5px solid #fff;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  animation: loader 2s linear infinite;
}

@keyframes loader {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
